import defaultIcon from "../../assets/github-mark.png"
import GenericLabel from "./GenericLabel"

export default function GenericButton( props ) {
    const src = props.symbol ? props.symbol : defaultIcon

    return props.symbol ? 
    (
        <div onClick={props.onClick} className="generic-button">
            <img src={src} alt="icon" style={{ width: "24px", height: "24px" }}></img>
            <GenericLabel value={props.children} size="20px"> </GenericLabel>
        </div> 
    ):(
        <div onClick={props.onClick} className="generic-button">
            <GenericLabel value={props.children} size="20px"> </GenericLabel>
        </div>    
    )
}